import { Data } from "./data.json";

// State Extractor

export const stateList = () =>[...new Set(Data.map(({ State }) => JSON.stringify(State)))].map((state) => JSON.parse(state));

// City Extractor

export const cityList = (s) =>[...new Set(Data.map(({ State, District }) => State === s ? JSON.stringify(District) : null))].map((city) => JSON.parse(city));

// Pincode Extractor

export const pincodeList = (c) =>[...new Set(Data.map(({ District, Pincode }) => District === c ? JSON.stringify(Pincode) : null))].map((pc) => JSON.parse(pc));
